/*************
Main Styling
**************/

nav a {
  text-decoration: none;
  padding-right: 2em;
}

nav a:hover {
  text-decoration: none;
}

/*************
App Styling
**************/


.App {
  text-align: center;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-intro {
  font-size: large;
}



/*************
Navigation Styling
**************/

.navbar-nav {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  padding-left: 2em;
}

.navbar-header {
  width: 100%;
  background-color: white;
}

/*************
Home Styling
**************/

.jumbotron {
  height: 400px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  background-image: url('../bg.jpg');
  background-size: cover;
  background-repeat: no-repeat;
  background-attachment: fixed;
}

.title, .sub-title {
  color: white;
  text-transform: uppercase;
}

/*************
About Styling
**************/

.about-content {
  padding-left: 2em;
  padding-right: 2em;
}

/*************
Footer Styling
**************/

.footer {
  padding-left: 2em;
}



